.App {
    text-align: center;
    background-image: url("./assets/coffeeshopimg.jpeg");
    background-position: center 72.5%;
    background-size: cover;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    display: table;
}

.Title {
    background-color: orange;
    padding: 30px 0;
    font-size: 30px;
    text-align: center;
    text-transform: uppercase;
    color: Red;
    font-weight: bold;
}

h1 {
    padding: 30px 0;
    font-size: 50px;
    text-align: center;
    text-transform: uppercase;
    color: ORANGE;
    font-weight: bold;
}