.drinkBlock {
    cursor: pointer;
    width: 250px;
}

.drinkBlock:hover .cup,
.drinkBlock:hover .bottom {
    background-color: black;
    transition: all 0.3s ease-in-out;
}

.drink {
    background-color: brown;
    height: 20px;
    border-radius: 50%;
    z-index: 99;
    position: relative;
    margin: 0 auto -28px;
    width: 233px;
}

.top {
    background-color: #dddddd;
    height: 20px;
    border-radius: 50%;
    border: 8px solid #ddd;
    z-index: 9;
    position: relative;
    box-shadow: 0 1px 3px 0 rgb(0 0 0 / 51%);
}

.cup {
    transition: all 0.3s ease-in-out;
    margin: -18px auto 0;
    background-color: #dddddd;
    height: 300px;
    width: 100%;
    max-width: 240px;
    clip-path: polygon(50% 250%, 0 0, 100% 0);
}

.bottom {
    background-color: #dddddd;
    height: 20px;
    border-radius: 50%;
    z-index: 9;
    position: relative;
    width: 144px;
    margin: -10px auto 0;
    transition: all 0.3s ease-in-out;
}

.smoke {
    position: relative;
    display: flex;
    z-index: 1;
    padding: 0 20px;
    justify-content: center;
}

.smoke span {
    position: relative;
    bottom: 50px;
    display: block;
    margin: 0 2px 50px;
    min-width: 8px;
    height: 120px;
    background: #ffffff;
    border-radius: 50%;
    animation: animate 5s linear infinite;
    opacity: 0;
    filter: blur(10px);
    animation: animate 5s linear infinite;
}

.jump {
    animation: clickSize .4s ease-in;
}

.jump .cup,
.jump .smoke span,
.jump .bottom {
    background-color: black;
}

@keyframes animate {
    0% {
        transform: translateY(0) scaleX(1);
        opacity: 0;
    }
    15% {
        opacity: 1;
    }
    50% {
        transform: translateY(-150px) scaleX(5);
    }
    95% {
        opacity: 0;
    }
    100% {
        transform: translateY(-300px) scaleX(10);
    }
}